* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.layout {
  border: 4px solid blue;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: blue;
  padding: 1rem 0;
  overflow: scroll;
}

.body-text {
  font-family: 'Open Sans', sans-serif;
  line-height: 1;
  display: inline-block;
  width: 15rem;
  font-size: 1.15rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.link {
  text-decoration: none;
  font-weight: 700;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.section--contact .body-text {
  text-align: center;
}

.heading {
  font-family: 'Inconsolata', monospace;
  letter-spacing: -1px;
  line-height: 1;
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

@media (min-width: 768px) {
  .heading {
    border: 2px solid blue;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

.icon-container {
  display: flex;
  justify-content: space-between;
  width: 15rem;
  margin-top: 1rem;
}

.icon {
  fill: blue;
  width: 1.75rem;
  height: 1.75rem;
}
